.details-container{
  margin-top: 25px;
}

.details-container .card {
  align-items: center;
  padding: 10px;
}

.details-container .card-img, .details-container .card-img-top {
  height: 380px;
  width: 380px;
}

.details-container .movie-name {
  text-align: center;
  font-size: 25px;
  font-weight: 900;
  margin-bottom: 20px;
}

.details-container .movie-details {
  font-size: 15px;
}

.details-container .details-data .data-label{
  color: #807d7d;
}

.details-container .details-data .data-value{
  text-transform: capitalize;
}

.details-container .details-fav-icon{
  cursor: pointer;
  margin-left: 10px;
}